<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import { useForm, useToast } from 'vuestic-ui'
import * as company$ from '../../../core/requests/General'

const { notify } = useToast()
const form: any = ref({})
const { validate } = useForm('formCompany')
const basic = ref([])
const isLoading = ref(false)

onMounted(async () => {
  await getData()
})

const toBase64 = async (file: any) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => resolve(reader.result)
    reader.onerror = reject
  })
}

async function getData() {
  isLoading.value = true
  const data: any = await company$.default.getCompany()
  form.value = {
    name: data.name,
    signature: data.signature,
    social_reason: data.social_reason,
    ruc: data.ruc,
    office: data.office,
    email: data.email,
    phone: data.phone,
    conditions: data.conditions,
  }
  isLoading.value = false
}

const doUpdate = async () => {
  try {
    validate()

    if (basic.value.length) {
      const signFile = await toBase64(basic.value[0])
      form.value.sign = signFile
    }

    await company$.default.update(form.value)

    notify({
      message: 'Actualizado correctamente',
      color: 'success',
    })
  } catch (e: any) {
    console.log(e)
  }
}
</script>

<template>
  <div class="flex flex-col space-y-6 md:space-y-4">
    <VaCard>
      <VaCardTitle class="flex justify-between">
        Datos de Empresa
        <VaButton class="ml-3 pr-2 pl-1" icon="save" color="primary" @click="doUpdate">Actualizar</VaButton>
      </VaCardTitle>
      <VaCardContent>
        <div>
          <div class="flex flex-col mt-4 px-4">
            <VaForm ref="formCompany" immediate class="gap-2">
              <VaInput
                v-model="form.name"
                stateful
                name="name"
                clearable
                label="Nombre de Empresa"
                class="mb-4"
                :loading="isLoading"
              />
              <VaInput
                v-model="form.social_reason"
                stateful
                name="social_reason"
                clearable
                label="Razón Social"
                class="mb-4"
                :loading="isLoading"
              />
              <VaInput v-model="form.ruc" stateful name="ruc" clearable label="RUC" class="mb-4" :loading="isLoading" />
              <VaInput
                v-model="form.office"
                stateful
                name="office"
                clearable
                label="Dirección de Oficina"
                class="mb-4"
                :loading="isLoading"
              />
              <VaInput
                v-model="form.email"
                stateful
                name="email"
                clearable
                label="Correo de Contacto"
                class="mb-4"
                :loading="isLoading"
              />
              <VaInput
                v-model="form.phone"
                stateful
                name="phone"
                clearable
                label="Teléfono de Contacto"
                class="mb-4"
                :loading="isLoading"
              />

              <div class="mb-4">
                <div v-if="form?.signature" class="my-2 mb-4">
                  <p class="mb-2" style="font-size: 11px; font-weight: 600; color: var(--va-primary)">Firma:</p>
                  <img :src="form?.signature" style="width: 120px" alt="" />
                </div>
                <VaFileUpload
                  v-model="basic"
                  dropzone
                  drop-zone-text="Arrastra o selecciona tu firma"
                  upload-button-text="Buscar"
                  file-types="jpg,jpeg,png"
                />
                <p v-if="basic?.length > 1" class="text-xxs bg-red-100 px-2">Solo se admite un archivo.</p>
              </div>

              <div class="mb-5">
                <p style="font-size: 11px; font-weight: 600; color: var(--va-primary)">Condiciones Generales:</p>
                <QuillEditor
                  v-model:content="form.conditions"
                  theme="snow"
                  content-type="html"
                  :toolbar="['bold', 'italic']"
                />
              </div>
            </VaForm>
          </div>
          <div class="flex justify-end">
            <VaButton class="pr-2 pl-1" icon="save" color="primary" @click="doUpdate">
              <span class="ml-2">Actualizar</span>
            </VaButton>
          </div>
        </div>
      </VaCardContent>
    </VaCard>
  </div>
</template>

<style>
.va-file-upload__field__text {
  font-size: 11px;
}

.va-button--small .va-button__content {
  font-size: 11px !important;
}
</style>
