<script setup lang="ts">
import { onMounted, reactive, ref } from 'vue'
import { useForm } from 'vuestic-ui'
import { useToast } from 'vuestic-ui'
import Employee from '../../../core/requests/Employee'
import { validators } from '../../../services/utils'

const emit = defineEmits(['close'])

const props = defineProps({
  data: {
    default: null,
    type: Object,
  },
})

const isLoading = ref(false)
const { isValid, validate } = useForm('formRef')
const form: any = reactive({
  ...(props.data && {
    names: props.data?.names,
    lastnames: props.data?.lastnames,
    email: props.data?.email,
    phone: props.data?.phone,
    status: props.data?.status,
  }),
})
const { notify } = useToast()

onMounted(() => {
  console.log(props.data)
})

const doProcess = async () => {
  if (validate()) {
    isLoading.value = true
    try {
      const body: any = {
        ...form,
        status: form.status ? 1 : 0,
      }

      const response: any = props.data ? await Employee.update(body, props.data.id) : await Employee.register(body)

      if (response.status == true) {
        notify({ message: 'Trabajador grabado correctamente', color: 'primary' })
        emit('close')
      } else {
        notify({ message: 'Error al grabar trabajador', color: 'danger' })
      }
    } catch (e: any) {
      console.log(e)
    }

    isLoading.value = false
  }
}
</script>

<template>
  <div class="bg-blue-50">
    <div class="mb-4">
      <div class="flex justify-between align-center justify-center">
        <h1 class="h5 font-bold m-0">{{ $props.data?.username ? 'Editar' : 'Nuevo' }} Trabajador</h1>
      </div>
    </div>

    <div class="new-employee gap-8">
      <VaForm ref="formRef" immediate class="gap-2">
        <div class="w-full bg-white p-2 pb-5 mb-4">
          <div class="gap-2">
            <div class="columns-1 mt-1">
              <div class="w-full mb-2">
                <VaInput v-model="form.names" :rules="[validators.required]" stateful clearable label="Nombres" />
              </div>

              <div class="w-full mb-2">
                <VaInput v-model="form.lastnames" :rules="[validators.required]" stateful clearable label="Apellidos" />
              </div>

              <div class="w-full mb-4">
                <VaInput v-model="form.phone" :rules="[validators.required]" stateful clearable label="Teléfono" />
              </div>

              <div class="w-full">
                <VaSwitch v-model="form.status" size="small" label="Estado" />
              </div>
            </div>
          </div>
        </div>
      </VaForm>
    </div>

    <div class="pb-3">
      <div class="flex justify-end">
        <div class="flex gap-2">
          <VaButton icon="save" color="primary" :disabled="!isValid" :loading="isLoading" @click="doProcess">
            <span class="text-xs px-1">Grabar Trabajador</span>
          </VaButton>
        </div>
      </div>
    </div>
  </div>
</template>

