import { API } from '../constants'

const services = {
  list: API + '/configurations/company',
}

const _token = localStorage.getItem('__token')

const updateConfig = async (body: any) => {
  const requestOptions = {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(services.list, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

const getData = async () => {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(services.list, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export default {
  update: updateConfig,
  getCompany: getData,
}
