<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import { format } from 'date-fns'
import NewEmployee from './NewEmployee.vue'
import Employee from '../../../core/requests/Employee'

const data: any = ref([])
const showConfirm: any = ref(null)
const isDeleting = ref(false)
const isLoading = ref(false)
const showNewUser = ref(false)
const selectedUser: any = ref(null)

onMounted(() => {
  getData()
})

const getData = async () => {
  isLoading.value = true
  showNewUser.value = false
  const _data: any = await Employee.getAll()
  data.value = _data.map((e: any) => ({
    ...e,
    status: !!e.status,
    loading: false,
    created_at: format(new Date(e.created_at), 'dd/MM/yyyy'),
  }))
  isLoading.value = false
}
const doDelete = async () => {
  isDeleting.value = true
  await Employee.delete(showConfirm.value?.id)
  showConfirm.value = null
  getData()
  isDeleting.value = false
}
const doDisable = async (e: any) => {
  e.loading = true
  await Employee.update(
    {
      names: e?.names,
      lastnames: e?.lastnames,
      // email: e?.email,
      phone: e?.phone,
      status: e?.status ? 1 : 0,
    },
    e?.id,
  )
  getData()
  e.loading = false
}
const select4Edit = (data: any) => {
  selectedUser.value = data
  showNewUser.value = true
}
const openNewUser = () => {
  showNewUser.value = true
  selectedUser.value = null
}
</script>

<template>
  <div class="flex flex-col space-y-6 md:space-y-4">
    <VaInnerLoading :loading="isLoading">
      <VaCard>
        <VaCardTitle class="d-flex justify-between">
          Administración de trabajadores
          <VaButton icon="add" color="primary" @click="openNewUser">
            <span class="px-1">Nuevo Trabajador</span>
          </VaButton>
        </VaCardTitle>
        <VaCardContent>
          <div class="flex flex-col p-4 bg-backgroundSecondary rounded-lg">
            <div class="va-table-responsive">
              <table class="va-table w-full text-xs">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Nombres Completos</th>
                    <!-- <th>Correo Electrónico</th> -->
                    <th>Teléfono</th>
                    <th>Estado</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="t in data" :key="t.id">
                    <td>00{{ t.id }}</td>
                    <td>{{ t.names }} {{ t.lastnames }}</td>
                    <!-- <td>{{ t.email }}</td> -->
                    <td>{{ t.phone }}</td>
                    <td>
                      <VaSwitch
                        v-model="t.status"
                        :loading="t.loading"
                        size="small"
                        @update:modelValue="doDisable(t)"
                      />
                    </td>
                    <td>
                      <div class="flex gap-1">
                        <VaButton icon="edit" color="primary" round @click="select4Edit(t)"></VaButton>
                        <VaButton icon="delete" color="danger" round @click="showConfirm = t"></VaButton>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </VaCardContent>
      </VaCard>
    </VaInnerLoading>

    <VaModal v-model="showNewUser" size="360px" close-button hide-default-actions blur no-dismiss>
      <div>
        <NewEmployee :data="selectedUser" @close="getData" />
      </div>
    </VaModal>

    <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss blur>
      <p class="text-sm">¿Deseas eliminar este trabajador?</p>

      <template #footer>
        <VaButton size="small" preset="secondary" class="mr-1" @click="showConfirm = null"> Cancelar</VaButton>
        <VaButton :loading="isDeleting" size="small" @click="doDelete"> Continuar</VaButton>
      </template>
    </VaModal>
  </div>
</template>
